<template>
  <div class="con">
    <publicTop></publicTop>
    <!-- 头部 -->
    <div
      class="head-bg"
      :style="`background-image: url(${url});background-size: 100% 100%;`"
    >
      <!-- <img src="@/assets/Mask.png" alt="" class="head-bg-img" /> -->

      <div class="head-bg-t">
        <div class="head-bg-l" @click="goBack">
          <img src="@/assets/icon-back-white.png" alt="" />
        </div>
        <div class="head-bg-text">培训活动</div>
      </div>
      <div class="head-bg-ending">
        <div class="zhao-head-div">
          <input
            type="text"
            v-model="inputContent"
            placeholder="请输入相关活动关键字"
          />
          <img src="@/assets/icon-search.png" alt="" @click="goSear" />
        </div>
      </div>
    </div>

    <!-- 底部列表 -->
    <div class="list-all" v-if="allL.length != 0">
      <van-list
        :immediate-check="false"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        loading-text="下拉查看全部"
        @load="kh"
      >
        <div
          v-for="(item, index) in allL"
          :key="index"
          class="list-all-li"
          @click="goDe(item.id)"
        >
          <div class="list-all-li-r">
            <div class="list-all-li-r-t">
              <div class="list-all-li-r-t-text">
                {{ item.title }}
              </div>
            </div>
            <div class="list-all-li-r-m">
              <img src="@/assets/gray.png" alt="" class="list-all-li-r-m-l" />
              <div class="list-all-li-r-m-r">{{ item.address }}</div>
            </div>
            <div class="list-all-li-r-r">
              <img src="@/assets/timehei.png" alt="" />
              <div class="list-all-li-r-r-text">
                {{ item.startTime.slice(0, 10) }}
              </div>
            </div>
          </div>
        </div>
      </van-list>
    </div>
    <!-- 没有数据时 -->
    <div class="list-all-no" v-if="allL.length == 0">
      <img src="@/assets/noData.png" alt="" class="list-all-no-img" />
      <div class="list-all-no-text">暂无数据</div>
    </div>
    <!-- <HomeTop :i="checkIndex" /> -->
  </div>
</template>

<script>
import qs from "qs";
import HomeTop from "@/components/homeTop.vue";
import publicTop from "@/components/publicTop.vue";
export default {
  name: "activityMore",
  components: {
    HomeTop,
    publicTop,
  },
  data() {
    return {
      // checkIndex: 1,
      tabIndex: 0,
      inputContent: "",
      url: require("../assets/Mask.png"),
      loading: false,
      finished: false,
      pageNo: 1,
      allL: [],
      bgc: this.GLOBAL.bgc,
    };
  },
  beforeRouteLeave(to, from, next) {
    console.log(to, from);
    if (to.path === "/activityDetail") {
      this.$store.commit("updataListActivity", this.allL);
      this.$store.commit("updataPageActivity", this.pageNo);
    } else {
      this.$store.commit("updataListActivity", []);
      this.$store.commit("updataPageActivity", "");
      this.$store.commit("updataInputActivity", "");
    }
    next();
  },
  watch: {
    inputContent(newV, oldV) {
      console.log(newV, oldV);
      this.$store.commit("updataInputActivity", newV);
    },
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    goSear() {
      console.log(this.inputContent, "search");

      this.$store.commit("updataInputActivity", this.inputContent);
      this.allL = [];
      this.kh();
    },
    goDe(id) {
      // this.$store.commit("updataDetailId", id);
      // this.$router.push({ name: "activityDetail" });
      this.$router.push({
        path: "/activityDetail",
        query: { id: id },
      });
    },

    kh() {
      let that = this;
      that.loading = true;
      console.log(that.$store.getters.getPageActivity, "页数");
      if (that.$store.getters.getPageActivity) {
        that.pageNo = that.$store.getters.getPageActivity;
      }
      if (that.pageNo == 1) {
        that.allL = [];
      }
      if (that.allL.length == 0) {
        that.pageNo = 1;
      }
      const params = {
        pageNo: that.pageNo,
        pageSize: 16,
        searchType: 1,
        title: that.inputContent,
      };
      console.log(params);
      that.$axios
        // .get(
        //   "/f/common/activity/ajax_activity?pageNo=" +
        //     that.pageNo +
        //     "&pageSize=16" +
        //     "&searchType=1" +
        //     "&title=" +
        //     that.inputContent
        // )
        .post("/f/common/activity/ajax_activity", qs.stringify(params))
        .then((res) => {
          console.log(res, that.allL, "数据");
          if (res.data.state == 1) {
            that.loading = false;
            if (that.pageNo <= res.data.data.totalPage) {
              ++that.pageNo;
              that.$store.commit("updataPageActivity", that.pageNo);
              if (res.data.data.list && res.data.data.list.length != 0) {
                that.allL = [...that.allL, ...res.data.data.list];
              }
              console.log(that.allL, that.allL.length, 56);
            } else {
              console.log("有吗");
              that.$store.commit("updataPageActivity", "");
              that.finished = true;
            }
          } else {
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
  },
  created() {
    console.log(
      this.allL.length,
      this.$store.getters.getPageActivity,
      typeof this.$store.getters.getPageActivity,
      "all"
    );
    if (this.$store.getters.getInputActivity) {
      this.inputContent = this.$store.getters.getInputActivity;
      console.log(this.inputContent);
    } else {
      this.inputContent = "";
    }
    if (this.$store.getters.getListActivity.length != 0) {
      this.allL = this.$store.getters.getListActivity;
      console.log(this.allL, "all");
    } else {
      this.kh();
    }
  },
  beforeDestory() {},
  destroyed() {},
  mounted: function () {},
};
</script>
<style lang="css" scoped>
.con {
  background-color: #f5f5f5;
  padding-bottom: 1.66rem;
  min-height: 100%;
}
.head-bg {
  width: 7.5rem;
  height: 2.4rem;
  padding: 0.4rem 0.32rem 0 0.32rem;
  box-sizing: border-box;
  position: relative;
  /* background-image: url("../assets/Mask.png");
  background-size: 100% 100%; */
}
/* 类型弹框 */
.pop-div {
  width: 7.5rem;
  height: 0.98rem;
  border-bottom: 0.02rem solid #eeeeee;
  text-align: center;
  line-height: 0.98rem !important;
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #3f4967;
}
.pop-div1 {
  width: 7.5rem;
  height: 0.98rem;
  text-align: center;
  line-height: 0.98rem !important;
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #3f4967;
}
