  width: 7.5rem;
  height: 0.14rem;
  background-color: #f5f5f5;
}
.pop-end {
  width: 7.5rem;
  height: 0.98rem;
  text-align: center;
  line-height: 0.98rem !important;
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
