  letter-spacing: 0rem;
  color: #9fa4b3;
}
/* 类型弹框 */
.head-bg-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.head-bg-t {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  height: 0.48rem;
  margin-bottom: 0.2rem;
}
.head-bg-ending {
  width: 100%;
  display: flex;
