  letter-spacing: 0rem;
  color: #ffffff;
}
.zhao-head-div {
  width: 100%;
  height: 0.72rem;
  background-color: #ffffff;
  box-shadow: 0rem 2rem 8rem 0rem rgba(71, 96, 215, 0.5);
