  margin-left: 0.1rem;
}
.footer-top {
  width: 7.5rem;
  height: 1rem;
  padding: 0 0.32rem;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer-top-l {
  width: 3rem;
  height: 1rem;
  display: flex;
  align-items: center;
}

.footer-top-r {
  height: 1rem;
  display: flex;
  align-items: center;
}

.footer-top-r-l {
  height: 0.5rem;
  background-color: #f0f0f0;
  border-radius: 0.04rem;
  margin-right: 0.2rem;
  display: flex;
