}

.footer-top-r-r {
  width: 0.94rem;
  height: 0.5rem;
  background-color: #f0f0f0;
  border-radius: 0.04rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-top-r-r img {
  width: 0.14rem;
  height: 0.08rem;
}
.footer-top-l-title {
  font-family: SourceHanSansCN-Regular;
  font-size: 0.28rem;
  font-weight: normal;
