  font-size: 0.3rem !important;
  font-weight: bold;
  font-stretch: normal;
  letter-spacing: -0.01rem;
  color: #2b2e34 !important;
}

.list-all-no {
  width: 7.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
