}
.list-all-no-text {
  font-family: PingFangSC-Regular;
  font-size: 0.32rem;
  font-weight: normal;
  font-stretch: normal;
  letter-spacing: 0rem;
  color: #9fa4b3;
}
.list-all {
  width: 7.5rem;
  padding: 0 0.32rem;
  box-sizing: border-box;
}
.list-all-li {
  width: 6.86rem;
  background-color: #ffffff;
  border-radius: 0.12rem;
  margin-bottom: 0.2rem;
  display: flex;
  padding: 0.4rem 0.32rem;
  box-sizing: border-box;
  position: relative;
}
.list-all-li-status {
  position: absolute;
  top: 0;
