}
.list-all-li-l {
  width: 1.2rem;
  height: 1.22rem;
  margin-right: 0.24rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.list-all-li-l img {
  width: 1.2rem;
  height: 1.22rem;
}
.list-all-li-r {
  width: 6.22rem;
}
.list-all-li-r-t {
  width: 6.22rem;
  display: flex;
  align-items: center;
}

.list-all-li-r-t-text {
  width: 6rem;
  font-family: PingFangSC-Medium;
